html {
  box-sizing: border-box;
  font-size: 16px;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.Grid {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}
.Grid--fixed {
  max-width: 100%;
  width: 1200px;
  margin: 0 auto;
}
.Grid--flex_cols .Grid__col {
  display: flex;
}
.Grid--gutters {
  margin: -1rem 0 1rem -1rem;
}
.Grid--gutters .Grid--gutters {
  margin: -1rem 0 1rem 0;
}
.Grid--gutters.Grid--fixed {
  margin: -1rem auto 1rem auto;
}
.Grid--gutters .Grid__col {
  padding: 1rem 0 0;
}
.Grid--gutters .Grid__col:first-child {
  padding-left: 0;
}
.Grid__col {
  flex: 0 0 100%;
}
.Grid__col--xs-1 {
  width: 8.33333333%;
  flex: none;
}
.Grid__col--xs-2 {
  width: 16.66666667%;
  flex: none;
}
.Grid__col--xs-3 {
  width: 25%;
  flex: none;
}
.Grid__col--xs-4 {
  width: 33.33333333%;
  flex: none;
}
