}
.Grid__col--xs-10 {
  width: 83.33333333%;
  flex: none;
}
.Grid__col--xs-11 {
  width: 91.66666667%;
  flex: none;
}
.Grid__col--xs-12 {
  width: 100%;
  flex: none;
}
